import { observable, computed, action } from 'mobx';

interface IStreamItem {
  stream: File;
  file_name: string;
}

/**
 * Хранилище информации о связи
 *
 * @param linkTotal - сумма
 * @param linkAutor - автор связи
 * @param linkDate - дата создания связи
 * @param linkSource - источник информации
 * @param linkComment - комментарий
 */
class CreateLinkStore {
  @observable linkTotal: any = '';
  @observable linkAutor: any = '';
  @observable linkDate: any = '';
  @observable linkSource: any = '';
  @observable linkComment: any = '';
  @observable isValidFile: boolean = false;
  @observable attach_documents: IStreamItem[] = [];
  @observable invalidFiles: string[] = [];
  @observable fileName: string = '';

  @observable allowTypes = {
    rtf: true,
    doc: true,
    docx: true,
    pdf: true,
    xls: true,
    xlsx: true,
    txt: true,
  };

  @action attachFiles = (files: File[]) => {
    this.invalidFiles = [];

    const streams: IStreamItem[] = files
      .map((file: File) => ({
        stream: file,
        file_name: file.name,
      }))
      .filter(({ file_name }) => {
        const fileExt = file_name.split('.').pop() as string;

        if (fileExt in this.allowTypes) {
          return true;
        }

        this.invalidFiles.push(file_name);
      });

    this.attach_documents = [...this.attach_documents, ...streams];
  };

  @action clearForm = () => {
    this.linkComment = '';
    this.linkTotal = '';
    this.linkSource = '';
    this.attach_documents = [];
    this.invalidFiles = [];
    this.fileName = '';
    this.isValidFile = false;
  };

  @computed get getTotal(): any {
    return this.linkTotal;
  }

  @computed get getAutor(): any {
    return this.linkAutor;
  }

  @computed get getDate(): any {
    return this.linkDate;
  }

  @computed get getSource(): any {
    return this.linkSource;
  }

  @computed get getComment(): any {
    return this.linkComment;
  }

  @action setIsValidFile = (data: any): void => {
    this.isValidFile = data;
  };

  @action changeTotal = (e: any) => {
    e.target.value = e.target.value
      .replace(/[^\d]/g, '')
      .toString()
      .replace(/\B(?=(\d{3})+(?!\d))/g, ' ');
    this.linkTotal = e.target.value;
  };

  @action changeSource = (e: any) => {
    this.linkSource = e.target.value;
  };

  @action changeComment = (e: any) => {
    this.linkComment = e.target.value;
  };
}

export default new CreateLinkStore();
