import React, { PureComponent } from 'react';
import styles from './InputInformation.module.scss';

import InputLabelEdit from '../../InputLabelEdit';
import createLinkStore from '../../createLinkStore';

type InputInformationProps = {
  disabled?: boolean;
  nameTitle: any;
  linkTotalError: boolean;
  linkSourceError: boolean;
  typeId: any;
  subtypeId: any;
};

type InputInformationState = {
  date: any;
};

/**
 * Компонент Аренда связи
 */
class InputInformation extends PureComponent<InputInformationProps, InputInformationState> {
  constructor(props: InputInformationProps) {
    super(props);

    const today = new Date(),
      date =
        (today.getDate() < 10 ? '0' + today.getDate() : today.getDate()) +
        '.' +
        (today.getMonth() + 1 < 10 ? '0' + (today.getMonth() + 1) : today.getMonth() + 1) +
        '.' +
        today.getFullYear() +
        ' ' +
        (today.getHours() < 10 ? '0' + today.getHours() : today.getHours()) +
        ':' +
        (today.getMinutes() < 10 ? '0' + today.getMinutes() : today.getMinutes()) +
        ':' +
        (today.getSeconds() < 10 ? '0' + today.getSeconds() : today.getSeconds());

    this.state = {
      date: date,
    };
  }

  componentDidMount() {
    const { date } = this.state;
    createLinkStore.linkDate = date;
  }

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const {
      props: { linkTotalError, linkSourceError, typeId, subtypeId },
      state: { date },
    } = this;

    return (
      <div>
        <div className={styles.data_container}>
          <div className={styles.dataHeader}>
            <span className={styles.labelBold}>Параметры связи</span>
            <span>
              Дата создания: <span className={styles.labelBold}>{date}</span>
            </span>
          </div>

          <div>
            {typeId === 1 && (
              <InputLabelEdit
                disabled={!subtypeId}
                className={`${styles.data_small} ${linkTotalError ? styles.error : ''}`}
                value={''}
                label="Сумма, ₽"
                onChange={() => {}}
                onKeyUp={(e) => createLinkStore.changeTotal(e)}
                maxLength={15}
                isRequired
                placeholder="0"
              />
            )}

            <InputLabelEdit
              disabled={!subtypeId}
              className={`${styles.data_big} ${styles.required} ${
                linkSourceError ? styles.error : ''
              }`}
              value={''}
              label="Источник информации"
              onChange={(e) => createLinkStore.changeSource(e)}
              isToolTip
              toolTipText="Заполнение источника информации для пользовательской связи"
              toolTipID="sourceToolTipLink"
              isRequired
              placeholder="Укажите источник информации"
            />

            <InputLabelEdit
              disabled={!subtypeId}
              className={styles.data_big}
              value={''}
              label="Комментарий"
              onChange={(e) => createLinkStore.changeComment(e)}
              placeholder="Ваш комментарий"
            />
          </div>
        </div>
      </div>
    );
  }
}

export default InputInformation;
