import React, { PureComponent } from 'react';
import styles from './CreateLinkDetail.module.scss';

import { inject, observer } from 'mobx-react';

import InputInformation from './components/InputInformation';
import ActionButton from 'components/ActionButton';
import Button from '../../Button/Button';
import createLinkStore from './createLinkStore';
import ValidationInput from '../../ValidationInput';

import { ModalStoreType } from '../../../stores/modalStore';
import { GraphStoreType } from '../../../stores/graph/graphStore';
import { DiagramStoreType } from '../../../stores/graph/diagramStore';
import AttachmentFiles from './components/AttachmentFiles';
import { ReactComponent as ErrorIcon } from 'components/FileUploader/assets/error.svg';
import { action } from 'mobx';
import { MODAL_DETAILED_INDIVIDUAL_PERSON } from '../constants/modalNames';

type CreateLinkDetailProps = {
  modalStore?: ModalStoreType;
  graphStore?: GraphStoreType;
  diagramStore?: DiagramStoreType;
  createLinkModeV3?: boolean;
  modeLink?: string;
  isFL?: boolean;
  onClose?: any;

  [key: string]: any;
};

type CreateLinkDetailState = {
  isShowError: boolean;
  labelRequired: any;
  isRequired: boolean;

  labelValidation: any;
  isValidation: boolean;

  linkTotalError: boolean;
  linkSourceError: boolean;
};

/**
 * Компонент для детализации по созданию связи
 */
@inject('modalStore', 'graphStore', 'diagramStore')
@observer
class CreateLinkDetail extends PureComponent<CreateLinkDetailProps, CreateLinkDetailState> {
  constructor(props: CreateLinkDetailProps) {
    super(props);
    this.state = {
      isShowError: false,
      labelRequired: [],
      labelValidation: [],
      isRequired: false,
      isValidation: false,
      linkTotalError: false,
      linkSourceError: false,
    };
  }

  @action componentDidMount(): void {
    createLinkStore.clearForm();
  }

  // Функция проверки на существование ошибок
  _checkError = (): void => {
    const { typeId } = this.props;
    let isTotalError: boolean = false;
    let isCommentError: boolean = false;
    let isTotalErrorValidation: boolean = false;
    let labelArray = [];
    let labelArrayValidation = [];

    let linkTotal = createLinkStore.linkTotal;
    let checkLinkTotal = linkTotal.replaceAll(' ', '').match(/^[0-9]+(?:,[0-9]{1,2})?$/);

    this.setState({
      labelRequired: [],
      labelValidation: [],
    });

    // Если тип связи - Кооперация
    if (typeId === 1) {
      // Если поле "Сумма, руб." пустое
      if (!createLinkStore.linkTotal) {
        labelArray.push('Сумма, руб.');
        this.setState({
          // Показывается блок с ошибками обязательности
          isRequired: true,
          // Показывается блок с ошибками
          isShowError: true,
          // Добавление в массив названия поля
          labelRequired: labelArray,
          // Проверка на то, что проблема в поле "Сумма, руб."
          linkTotalError: true,
        });
        isTotalError = true;
      } else {
        // Если не верный формат ввода данных в поле "Сумма, руб."
        if (!checkLinkTotal) {
          labelArrayValidation.push('Сумма, руб.');
          this.setState({
            // Показывается блок с ошибками обязательности
            isValidation: true,
            // Показывается блок с ошибками
            isShowError: true,
            // Добавление в массив названия поля
            labelValidation: labelArrayValidation,
            // Проверка на то, что проблема в поле "Сумма, руб."
            linkTotalError: true,
          });
          isTotalErrorValidation = true;
        } else {
          this.setState({ linkTotalError: false, isValidation: false });
          isTotalErrorValidation = false;
          isTotalError = false;
        }
      }
    }

    if (!createLinkStore.linkSource) {
      labelArray.push('Источник информации');
      this.setState({
        isRequired: true,
        isShowError: true,
        labelRequired: labelArray,
        linkSourceError: true,
      });
      isCommentError = true;
    } else {
      this.setState({ linkSourceError: false });
      isCommentError = false;
    }

    if (!isCommentError && !isTotalError && !isTotalErrorValidation) {
      this.setState({ isShowError: false, isValidation: false, isRequired: false });
      this._createNewLink();
    }
  };

  async _createNewLink() {
    const { modalStore } = this.props;

    modalStore?.closeModal('MODAL_CREATE_LINK');
    const { fromNode, toNode, typeId, subtypeId, graphStore, createLinkModeV3, modeLink, isFL } =
      this.props;
    if (createLinkModeV3) {
      if (isFL) {
        if (modeLink === 'from') {
          await graphStore?.createNodeFL({ phys_person: fromNode }, createLinkModeV3).then(() => {
            graphStore
              ?.fetchGraph(graphStore?.previouslyFetchedGraphId, null, false, createLinkModeV3)
              .then(() => {
                graphStore!.createNewLink({
                  source_id: graphStore?.nodeIDMoveV3,
                  target_id:
                    graphStore?.graphTransformMode === 'ModeV3' &&
                    toNode.indicators_bottom?.some((item: any) => item.name === 'Zombie')
                      ? toNode.key
                      : toNode.orig_id,
                  type_id: typeId,
                  subtype_id: subtypeId,
                  description: createLinkStore.linkComment,
                  value: createLinkStore.linkTotal.replaceAll(' ', ''),
                  info_source: createLinkStore.linkSource,
                  attach_documents: createLinkStore.attach_documents,
                });
              });
          });
        } else {
          await graphStore?.createNodeFL({ phys_person: toNode }, createLinkModeV3).then(() => {
            graphStore
              ?.fetchGraph(graphStore?.previouslyFetchedGraphId, null, false, createLinkModeV3)
              .then(() => {
                graphStore!.createNewLink({
                  source_id:
                    graphStore?.graphTransformMode === 'ModeV3' &&
                    fromNode.indicators_bottom?.some((item: any) => item.name === 'Zombie')
                      ? fromNode.key
                      : fromNode.orig_id,
                  target_id: graphStore?.nodeIDMoveV3,
                  type_id: typeId,
                  subtype_id: subtypeId,
                  description: createLinkStore.linkComment,
                  value: createLinkStore.linkTotal.replaceAll(' ', ''),
                  info_source: createLinkStore.linkSource,
                  attach_documents: createLinkStore.attach_documents,
                });
              });
          });
        }
      } else {
        if (modeLink === 'from') {
          await graphStore?.createNode(fromNode, createLinkModeV3).then(() => {
            graphStore
              ?.fetchGraph(graphStore?.previouslyFetchedGraphId, null, false, createLinkModeV3)
              .then(() => {
                graphStore!.createNewLink({
                  source_id: graphStore?.nodeIDMoveV3,
                  target_id:
                    graphStore?.graphTransformMode === 'ModeV3' &&
                    toNode.indicators_bottom?.some((item: any) => item.name === 'Zombie')
                      ? toNode.key
                      : toNode.orig_id,
                  type_id: typeId,
                  subtype_id: subtypeId,
                  description: createLinkStore.linkComment,
                  value: createLinkStore.linkTotal.replaceAll(' ', ''),
                  info_source: createLinkStore.linkSource,
                  attach_documents: createLinkStore.attach_documents,
                });
              });
          });
        } else {
          await graphStore?.createNode(toNode, createLinkModeV3).then(() => {
            graphStore
              ?.fetchGraph(graphStore?.previouslyFetchedGraphId, null, false, createLinkModeV3)
              .then(() => {
                graphStore!.createNewLink({
                  source_id:
                    graphStore?.graphTransformMode === 'ModeV3' &&
                    fromNode.indicators_bottom?.some((item: any) => item.name === 'Zombie')
                      ? fromNode.key
                      : fromNode.orig_id,
                  target_id: graphStore?.nodeIDMoveV3,
                  type_id: typeId,
                  subtype_id: subtypeId,
                  description: createLinkStore.linkComment,
                  value: createLinkStore.linkTotal.replaceAll(' ', ''),
                  info_source: createLinkStore.linkSource,
                  attach_documents: createLinkStore.attach_documents,
                });
              });
          });
        }
      }
    } else {
      await graphStore!.createNewLink({
        source_id:
          graphStore?.graphTransformMode === 'ModeV3' &&
          fromNode.indicators_bottom?.some((item: any) => item.name === 'Zombie')
            ? fromNode.key
            : fromNode.orig_id,
        target_id:
          graphStore?.graphTransformMode === 'ModeV3' &&
          toNode.indicators_bottom?.some((item: any) => item.name === 'Zombie')
            ? toNode.key
            : toNode.orig_id,
        type_id: typeId,
        subtype_id: subtypeId,
        description: createLinkStore.linkComment,
        value: createLinkStore.linkTotal.replaceAll(' ', ''),
        info_source: createLinkStore.linkSource,
        attach_documents: createLinkStore.attach_documents,
      });
    }
  }

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const { nameTitle, typeId, subtypeId, onClose } = this.props;
    const {
      isShowError,
      labelRequired,
      isRequired,
      linkTotalError,
      linkSourceError,
      labelValidation,
      isValidation,
    } = this.state;

    const disabled = false;

    const buttonDisabled =
      typeId === 1
        ? !createLinkStore.linkTotal || !createLinkStore.linkSource
        : !createLinkStore.linkSource;

    return (
      <div className={styles.container}>
        <div className={styles.input_information_container}>
          <InputInformation
            disabled={disabled}
            nameTitle={nameTitle}
            linkTotalError={linkTotalError}
            linkSourceError={linkSourceError}
            typeId={typeId}
            subtypeId={subtypeId}
          />
        </div>

        {isShowError && (
          <ValidationInput
            labelRequired={labelRequired}
            isRequired={isRequired}
            labelValidation={labelValidation}
            isValidation={isValidation}
          />
        )}
        <div className={styles.input_information_container}>
          {createLinkStore?.invalidFiles?.length > 0 &&
            createLinkStore?.invalidFiles.map((file) => (
              <div className={`${styles.container_validation}`}>
                <ErrorIcon className={`${styles.error_icon}`} />
                Указанный файл {file} имеет неподдерживаемый формат или испорчен. Укажите корректный
                файл в одном из форматов: .rtf, .doc, .docx, .pdf, .xls, .xlsx, .txt.
              </div>
            ))}

          <AttachmentFiles />
        </div>

        <div className={styles.buttons}>
          <Button onClick={onClose} mode="transparent">
            Отменить
          </Button>
          <Button disabled={buttonDisabled} onClick={this._checkError}>
            Добавить связь
          </Button>
        </div>
      </div>
    );
  }
}

export default CreateLinkDetail;
