import React, { BaseSyntheticEvent } from 'react';
import { inputWithLabelPropTypes } from './InputLabelEdit.props';
import styles from './InputLabelEdit.module.scss';
import ReactTooltip from 'react-tooltip';

type InputWithLabelProps = {
  label: string;
  onChange: (event: BaseSyntheticEvent) => void;
  onKeyUp?: (event: BaseSyntheticEvent) => void;
  value: string;
  className?: string;
  disabled?: boolean;
  isToolTip?: boolean;
  toolTipText?: string;
  toolTipID?: string;
  maxLength?: any;
  isRequired?: boolean;
  placeholder?: string;
};

/**
 * Компонент поля ввода с надписью
 *
 * @param {string} label - надпись над полем ввода
 * @param {func} onChange - функция обрабатывающая вводимый текст
 * @param {string} value - значение поля ввода
 * @param {string} [className] - CSS класс стилей
 * @param {boolean} [disabled] - флаг показывающий блокировать ли элемент на изменение
 * @param {boolean} isToolTip - наличие всплывающей подсказки
 * @param {string} toolTipText - текст всплывающей подсказки
 * @param {string} toolTipID - id всплывающей подсказки
 * @param {any} maxLength - максимальное количество символов
 */
const InputLabelEdit = (props: InputWithLabelProps) => {
  const {
    label,
    onChange,
    onKeyUp,
    value,
    disabled,
    isToolTip,
    toolTipText,
    toolTipID,
    maxLength,
    isRequired,
    placeholder,
    ...rest
  } = props;

  return (
    <div {...rest}>
      <span className={styles.label}>
        {label} {isRequired && <span className={styles.required}>(обязательно)</span>}
      </span>
      <input
        disabled={disabled}
        className={styles.custom_input}
        type="text"
        defaultValue={value}
        onChange={onChange}
        onKeyUp={onKeyUp}
        data-tip={isToolTip ? 'React-tooltip' : ''}
        data-for={isToolTip ? toolTipID : null}
        maxLength={maxLength}
        placeholder={placeholder}
      />
      {isToolTip ? (
        <ReactTooltip place="bottom" type="dark" effect="float" id={toolTipID} delayShow={200}>
          <span className={styles.spanTooltip}>{toolTipText}</span>
        </ReactTooltip>
      ) : (
        ''
      )}
    </div>
  );
};

InputLabelEdit.propTypes = inputWithLabelPropTypes;

InputLabelEdit.defaultProps = {
  disabled: false,
};

export default InputLabelEdit;
