import React, { PureComponent } from 'react';
import styles from './ValidationInput.module.scss';

import { ReactComponent as ErrorIcon } from './assets/error.svg';

type ValidationInputProps = {
  // Массив с названием полей, где возникает ошибка обязательности
  labelRequired?: any;
  // Массив с названием полей, где возникает ошибка валидации
  labelValidation?: any;
  // Отображение блока ошибок обязательности заполнения полей
  isRequired?: boolean;
  // Отображение блока ошибок валидации
  isValidation?: boolean;
};

/**
 * Компонент для отображения ошибок валидации
 */
class ValidationInput extends PureComponent<ValidationInputProps> {
  render() {
    const { labelRequired, isRequired, isValidation, labelValidation } = this.props;

    return (
      <div className={`${styles.container}`}>
        <div className={`${styles.header}`}>Ошибки валидации</div>
        {isRequired && (
          <div>
            {labelRequired.map((item: any, index: number) => (
              <div key={index} className={`${styles.item}`}>
                <ErrorIcon className={`${styles.error_icon}`} />
                Поле "{item}" обязательно для заполнения
              </div>
            ))}
          </div>
        )}
        {isValidation && (
          <div>
            {labelValidation.map((item: any, index: number) => (
              <div key={index} className={`${styles.item}`}>
                <ErrorIcon className={`${styles.error_icon}`} />
                Данные в поле "{item}" не соответствуют формату ввода
              </div>
            ))}
          </div>
        )}
      </div>
    );
  }
}

export default ValidationInput;
